// Single Listing
.atbd_content_active #directorist.atbd_wrapper {
    .atbd_single_listing {
        margin-bottom: 30px;
        background: $light;
        border-radius: 4px;
    
        &.atbd_listing_list {
          .atbd_single_listing_wrapper {
            display: flex;
    
            .atbd_listing_thumbnail_area {
              flex: 1;
    
              a {
                display: block;
                min-height: 100%;
              }
    
              img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 3px 0 0 3px;
              }
            }
    
            .atbd_listing_info {
              flex: 2;
              display: flex;
              flex-direction: column;
              align-content: space-between;
              justify-content: space-between;
    
            }
          }
    
          .atbdp_mark_as_fav {
            background: rgba(#000, 0.07);
            transition: $transition-base;
    
            .atbd_fav_icon::after {
              color: $light-gray;
            }
    
            &.atbdp_fav_isActive {
              background: $primary;
    
              .atbd_fav_icon::after {
                color: #fff;
                font-size: 14px;
              }
    
              &:hover {
                .atbd_fav_icon::after {
                  color: #fff;
                }
              }
            }
    
            &:hover {
              .atbd_fav_icon::after {
                color: $body-color;
              }
            }
          }
    
          .atbd-list-cover-top-right {
            margin: -3px;
    
            .atbdp_add_to_fav_listings,
            .atdlc_compare-btn-container,
            .atbd_badge {
              margin: 3px;
            }
    
            .atbdp_mark_as_fav,
            .atdlc-mark_compare_btn {
              background-color: #00000030;
            }
    
            .atbdp_mark_as_fav {
              .atbd_fav_icon {
                &:before {
                  color: #fff;
                }
              }
            }
          }
    
          .atbd-list-body-right {
            display: flex;
            margin: -3px;
            align-items: center;
    
            .atdlc_compare-btn-container {
              margin: 0 3px;
    
              .atdlc-mark_compare_wrapper.atdlc_list-btn-position {
                position: relative;
                right: 0;
                top: 0;
              }
    
              .atdlc-mark_compare_btn {
    
                i {
                  color: #9192a3;
                }
              }
            }
    
            .atbdp_mark_as_fav,
            .atdlc-mark_compare_btn {
              background-color: #F2F2F2;
    
              i,
              svg {
                color: #51526E;
              }
            }
    
            .atbdp_mark_as_fav {
              &::before {
                color: #51526E !important;
              }
            }
    
            .atdlc_compare-btn-container,
            .atbdp_add_to_fav_listings {
              margin: 3px;
            }
          }
    
          .atbd-list-body-top {
            .atbd_listing_meta {
              display: inline-flex;
              margin: 0;
    
              &:last-child {
                margin-left: 3px;
              }
            }
    
            .atbd_listing_transparent {
              padding: 0;
              background-color: #fff !important;
    
              i,
              svg {
                font-size: 12px;
                margin: 0;
                color: $warning;
              }
    
              .atbd_listing_avg {
                font-size: 15px;
                font-weight: 600;
                color: $dark;
              }
            }
    
            .atbd_listing_price {
              font-size: 15px;
              background-color: #fff !important;
              color: $dark;
              font-weight: 700;
              padding: 0;
            }
    
            .atbdp_info_list {
              position: relative;
    
              &:not(:last-child) {
                padding-right: 8px;
    
                &:after {
                  position: absolute;
                  right: 0;
                  top: 50%;
                  transform: translateY(-50%);
                  border-radius: 10px;
                  width: 1px;
                  height: 15px;
                  content: '';
                  background-color: #E9E9E9;
                }
    
                &.atdlc_compare-btn-container,
                &.atbdp_add_to_fav_listings {
                  &::after {
                    top: 56%;
                  }
                }
    
                &.atbd_badge {
                  margin-right: 7px !important;
    
                  &:after {
                    right: -8px;
                  }
                }
    
                &.atbd_badge+.atbd_badge {
                  margin-left: 8px !important;
    
                  &.atbd_badge_open,
                  &.atbd_badge_close {
                    margin-left: 4px !important;
                    padding-right: 0 !important;
                    padding-left: 4px !important;
                  }
                }
    
                &.atbd_badge+.atbdp_add_to_fav_listings,
                &.atbd_badge+.atdlc_compare-btn-container {
                  margin-left: 8px !important;
                }
              }
            }
    
            .atbd_badge {
    
              &.atbd_badge_open,
              &.atbd_badge_close {
                padding-right: 0px !important;
                padding-left: 0px !important;
              }
            }
    
            .atbd_listing_transparent {
              padding: 0;
              background-color: #fff !important;
              margin: -1.5px;
    
              i,
              svg {
                font-size: 12px;
                margin: 1.5px;
                color: $warning;
              }
    
              .atbd_listing_avg {
                font-size: 14px;
                font-weight: 700;
                color: #51526E;
                margin: 0 3px 0 6px;
              }
            }
    
            .atbd_listing_price {
              font-size: 15px;
              background-color: #fff !important;
              color: $dark;
              font-weight: 700;
              padding: 0;
    
            }
    
            .atbd_badge.atbd_badge_open,
            .atbd_badge.atbd_badge_close {
              position: relative;
              font-size: 12px;
              font-weight: 500;
              text-transform: capitalize;
              background-color: #fff !important;
            }
    
            .atbd_badge.atbd_badge_close {
              color: $danger;
            }
    
            .atbd_badge.atbd_badge_open {
              color: $success;
            }
    
            .atbdp_add_to_fav_listings {
              margin-left: 8px !important;
            }
    
            .atbd_listing_average_pricing {
              span {
                font-weight: 700;
              }
            }
    
            .atbd_listing_meta,
            .atbd_listing_meta,
            .atbd_listing_title,
            .atbd_badge,
            .atbdp_add_to_fav_listings,
            .atdlc_compare-btn-container {
              margin: 4px !important;
            }
    
            .atdlc_compare-btn-container,
            .atbdp_add_to_fav_listings {
              display: inline-flex;
            }
          }
    
          .atbd-list-cover-top-right,
          .atbd-card-cover-top-left,
          .atbd-list-cover-bottom-left,
          .atbd-list-cover-bottom-right,
          .atbd-list-body-top {
            .atdlc_compare-btn-container {
              .atdlc-mark_compare_wrapper.atdlc_list-btn-position {
                position: relative;
                right: 0;
                top: 0;
              }
            }
          }
    
          .atbd_listing_bottom_content {
    
            .atbd_content_right,
            .atbd_content_left {
              .atdlc-mark_compare_wrapper.atdlc_list-btn-position {
                margin: 0;
                position: relative;
                right: 0;
                top: 0;
              }
    
              .atdlc-mark_compare_btn {
                margin: 0;
              }
    
              .atbdp_mark_as_fav,
              .atdlc-mark_compare_btn {
                background-color: #F2F2F2;
                color: #51526E;
              }
    
              .atbd_fav_icon {
                &:before {
                  color: #51526E;
                }
              }
    
              .atbd_count {
    
                span.la,
                i {
                  position: relative;
                  top: 1.5px;
                }
              }
            }
    
            .atbd_content_right {
              .atbd_count+.atbd_author {
                margin-left: 20px;
              }
            }
          }
    
          &.directorist-list-nothumb {
            border-radius: 5px;
            border: 1px solid #EFF1F6;
    
            .atbdp_mark_as_fav,
            .atdlc-mark_compare_btn {
              background-color: #F2F2F2;
              color: #51526E;
            }
    
            .atbdp_mark_as_fav {
              .atbd_fav_icon {
                &:before {
                  color: #51526E;
                }
              }
            }
    
            .atdlc-mark_compare_wrapper.atdlc_list-btn-position {
              position: relative;
              right: 0;
