              }
    
              .atbd_badge.atbd_badge_close {
                color: $danger;
              }
    
              .atbd_badge.atbd_badge_open {
                color: $success;
              }
    
              .atbdp_add_to_fav_listings {
                margin-left: 8px !important;
              }
    
              .atbd_listing_average_pricing {
