                  font-weight: 700;
                }
              }
    
              .atbd_listing_meta,
              .atbd_listing_meta,
              .atbd_listing_title,
              .atbd_badge,
              .atbdp_add_to_fav_listings,
              .atdlc_compare-btn-container {
                margin: 4px !important;
              }
    
              .atbd_listing_meta {
                .atbd_listing_rating {
                  .atbd_listing_avg {
                    margin-left: 5px;
                    position: relative;
                    top: 1px;
                  }
                }
              }
    
              .atbd_listing_meta,
              .atbd_badge,
              .atbd_listing_title,
              .atbdp_add_to_fav_listings,
              .atdlc_compare-btn-container {
                margin: 4px;
              }
    
              .atbdp_add_to_fav_listings,
              .atdlc_compare-btn-container {
                display: inline-flex;
              }
            }
    
            .atbd-list-body-right {
              margin: -3px;
    
              .atbd_badge,
              .atbdp_add_to_fav_listings,
              .atdlc-mark_compare_btn {
                margin: 3px;
              }
            }
    
            .atbd_content_upper {
              padding: 20px 20px 28px;
            }
    
            .atbd_listing_bottom_content {
              padding: 14.5px 25px;
              border-color: #F2F2F2;
    
              .atbd_content_right {
                margin: -3px;
    
                .atbd_count {
    
                  span.la,
                  i {
                    position: relative;
                    top: 1.5px;
                  }
                }
    
                .atbd_listing_category+.atbd_author {
                  margin-left: 20px;
                }
    
                .atbd_count+.atbd_author {
                  margin-left: 20px;
                }
    
                .atbdp_add_to_fav_listings,
                .atdlc-mark_compare_btn {
                  margin: 3px;
                }
              }
            }
          }
        }
    
        &.atbd_listing_no_image {
          .atbd_single_listing_wrapper {
            display: block;
          }
    
          .atbd_listing_thumbnail_area {
            .atbd_lower_badge {
              padding: 10px 20px;
              position: static;
              display: block;
    
              &.atbd-no-spacing {
                padding: 0;
              }
            }
          }
    
          .atbd_listing_info {
            border-left: 0 none;
            border-right: 0 none;
          }
        }
    
        .atbd_listing_thumbnail_area {
          margin: 0;
          position: relative;
    
          .atbd_upper_badge {
            position: absolute;
            top: 15px;
            left: 15px;
          }
    
          .atbd_lower_badge {
            position: absolute;
            bottom: 15px;
            left: 15px;
          }
    
          .atbd-card-cover-top-left,
          .atbd-card-cover-top-right,
          .atbd-card-cover-bottom-left,
          .atbd-card-cover-bottom-right {
            position: absolute;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            margin: 0 -3px;
          }
    
          .atbd-card-cover-top-left,
          .atbd-card-cover-top-right,
          .atbd-card-cover-bottom-left,
          .atbd-card-cover-bottom-right {
    
            .atbd_badge,
            .atdlc_compare-btn-container,
            .atbdp_add_to_fav_listings {
              margin: 3px;
            }
    
            .atbdp_mark_as_fav {
              background-color: rgba(0, 0, 0, 0.2);
    
              .atbd_fav_icon {
                &:before {
                  color: #fff;
                }
              }
            }
          }
    
          .atbd-card-cover-top-left {
            left: 20px;
            top: 20px;
          }
    
          .atbd-card-cover-top-right {
            top: 20px;
            right: 20px;
          }
    
          .atbd-card-cover-bottom-left {
            left: 20px;
            bottom: 30px;
          }
    
          .atbd-card-cover-bottom-right {
            right: 20px;
            bottom: 30px;
          }
        }
    
        .atbd_listing_info {
          border: 1px solid $border-color;
          border-radius: 0 0 5px 5px;
    
          .atbd_content_upper {
            padding: 20px 20px 15px;
            position: relative;
    
            .atbd_excerpt_content {
              margin-top: 15px;
    
              a {
                font-size: 13px;
                font-weight: 500;
                color: $primary;
              }
            }
    
            .atbd_listing_title {
              padding: 0;
              text-transform: none;
              line-height: 1.2;
              margin-bottom: 8px !important;
    
              a {
                font-size: 18px;
              }
            }
    
            .atbd_listing_tagline {
              color: #7a82a6;
              line-height: 26px;
              font-size: 15px;
              margin-bottom: 7px;
            }
    
            .atbd_listing_data_list {
              margin-top: 13px;
    
              ul li {
                &:last-child {
                  p {
                    margin-bottom: 0;
                  }
                }
    
                p {
                  margin-bottom: 2px;
                  font-size: 14px;
                  display: flex;
                  align-items: center;
    
                  span,
                  i {
                    @extend .atbd_color-primary;
                    font-size: 15px;
                    margin-right: 8px;
    
                    &.la {
                      font-size: 17px;
                    }
                  }
    
                  a {
                    font-size: 14px;
                    word-break: break-all;
                  }
                }
              }
            }
          }
    
          .atbd_listing_bottom_content {
            border-top: 1px solid $border-color;
            padding: 12px 20px;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
    
            .atbd_content_left {
              margin-right: 12px;
    
              .atbd_count {
                span {
                  margin-right: 5px;
                }
              }
            }
    
            .atbd_content_right {
              display: flex;
              align-items: center;
              margin: 0 -5px;
    
              div {
                margin: 0 5px;
              }
    
              li+li {
                margin-left: 6px;
              }
    
              .atbd_count {
                span {
                  margin-right: 5px;
                }
              }
    
              .atbd_save {
                span {
                  color: #b2b7d0;
                  line-height: 28px;
                  width: 28px;
                  font-size: 14px;
                  border-radius: 50%;
                  text-align: center;
                  border: 1px solid $border-color;
                }
              }
    
              .atbd_author {
                a {
                  width: 28px;
                  height: 28px;
                  display: inline-block;
                  vertical-align: middle;
    
                  img {
                    border-radius: 50%;
                    width: 100%;
                    height: 100%;
                  }
                }
              }
            }
          }
        }
    
        &.atbd_listing_card {
