              min-height: 100%;
            }
    
            img {
              width: 100%;
              margin: 0;
              border-radius: 3px 3px 0 0;
    
              &.avatar {
                border-radius: 50%;
              }
            }
    
            .atbd_author {
              position: absolute;
              bottom: 0;
              transform: translateY(50%);
              width: 46px;
              height: 46px;
              z-index: 1;
    
              a {
                border: 3px solid #fff;
                display: block;
                border-radius: 300px;
              }
    
              img {
                height: auto;
                -webkit-border-radius: 300px;
                -moz-border-radius: 300px;
                border-radius: 300px;
              }
    
              .tooltip {
                min-width: 100px;
              }
    
              &.alignment-left {
                left: 20px;
              }
    
              &.alignment-center {
                left: 50%;
                transform: translate(-50%, 50%);
              }
    
              &.alignment-right {
                right: 20px;
              }
            }
          }
    
