                font-weight: 700;
              }
            }
    
            .atbd_listing_meta,
            .atbd_listing_meta,
            .atbd_listing_title,
            .atbd_badge,
            .atbdp_add_to_fav_listings,
            .atdlc_compare-btn-container {
              margin: 4px !important;
            }
    
            .atbd_listing_title {
              margin-bottom: 8px !important;
            }
    
            .atdlc_compare-btn-container {
              .atdlc-mark_compare_btn {
                width: 30px;
                height: 30px;
              }
            }
    
          }
    
          .directorist-card-nothumb-area {
            padding: 20px 20px 15px 20px;
            display: flex;
            justify-content: space-between;
    
            .directorist-card-nothumb-area__left {
              display: flex;
              align-items: center;
    
              .directorist-card-nothumb-1 {
                .atbd_author {
                  img {
                    border-radius: 50%;
                    min-width: 40px;
                  }
                }
              }
    
              .directorist-card-nothumb-2 {
                margin-left: 12px;
    
                .atbd_listing_title {
                  font-size: 18px;
                  font-weight: 500;
                  color: $dark;
                  margin-bottom: 0;
                }
              }
            }
    
            .directorist-card-nothumb-area__right {
              .directorist-card-nothumb-3 {
    
                .atbdp_mark_as_fav,
                .atdlc-mark_compare_btn {
                  color: #fff;
                  background-color: #CCCCCC;
                }
    
                .atbd_fav_icon {
                  &:before {
                    color: #fff;
                  }
                }
              }
            }
          }
    
          .directorist-card-nothumb-info {
            display: flex;
            align-items: center;
            margin: -4px;
            padding: 0px 20px 15px;
    
            .atbd_listing_meta {
              position: relative;
              top: 1px;
              margin-top: 0;
    
              .atbd_meta {
                line-height: 1.5;
                padding: 0;
                color: #fff;
                background-color: #fff !important;
              }
    
              .atbd_listing_price {
                font-size: 15px;
                background-color: #fff !important;
                color: #202428;
                font-weight: 700;
              }
    
              .atbd_meta.atbd_listing_rating {
                display: flex;
                align-items: center;
                padding: 0;
    
                i {
                  font-size: 12px;
                  margin: 1.5px;
                  color: #ffaf00;
                }
    
                .atbd_listing_avg {
                  line-height: 1;
                  position: relative;
                  top: 1px;
                  font-size: 14px;
                  font-weight: 700;
                  color: #51526E;
                  margin: 0 3px 0 6px;
                }
              }
            }
    
            .atbd_badge {
              line-height: 2.1;
              border-radius: 4px;
              padding: 0 6.65px;
            }
    
            .atbd_listing_meta,
            .atbd_badge {
              margin: 4px;
            }
          }
    
          .directorist-card-nothumb-info+.atbd_listing_info {
            border: 0 none;
    
            .atbd_content_upper {
              padding-top: 0;
    
              .atbd_listing_data_list {
                margin-top: 0;
              }
            }
    
            .atbd_listing_bottom_content {
              border-color: #F1F2F6;
            }
    
            .atbd_content_right {
              .atbd_count {
    
                span,
                i {
                  position: relative;
                  top: 1.5px;
                }
              }
            }
          }
    
          &.directorist-card-no-thumbnail {
            border-radius: 5px;
            box-shadow: 0 10px 20px #868EAE30;
          }
        }
    
        .atdlc-mark_compare_btn {
          width: 30px;
          height: 30px;
        }
    }
}

// Listing Favourite
#directorist.atbd_wrapper {
  .atbdp_mark_as_fav {
    background-color: #F2F2F2;
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;

    &.atbdp_fav_isActive {
      background: rgba(0, 0, 0, 0.5);

      .atbd_fav_icon {
        &:before {
          content: '\f233';
        }
      }
    }

    .atbd_fav_icon {
      line-height: 1;

      &:before {
        content: "\f234";
        font-family: 'LineAwesome';
        color: #51526E;
      }
    }

    .atbd_fav_tooltip {
      span {
        position: absolute;
        min-width: 120px;
        right: 0;
        top: 35px;
        background: #000;
        color: #fff;
        font-size: 13px;
        border-radius: 3px;
        text-align: center;
        padding: 2px 5px;

        &::before {
          content: '';
          position: absolute;
          border-bottom: 8px solid #000;
          border-right: 6px solid transparent;
          border-left: 6px solid transparent;
          right: 8px;
          top: -7px;
        }
      }
    }
  }
}

// Listing Info
#directorist.atbd_wrapper .atbd_single_line_card_info {
    .atbd_listing_info {
      .atbd_content_upper {
  
        .atbd_listing_title,
        .atbd_listing_tagline,
        .atbd_listing_data_list ul li p,
        .atbd_excerpt_content {
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
}
// Listing List
#directorist.atbd_wrapper {
    .atbd_listing_list {
        .atbdp_mark_as_fav .atbd_fav_icon:before {
          color: #9192a3;
        }
    
        .atbdp_mark_as_fav.atbdp_fav_isActive .atbd_fav_icon::before {
          color: #fff !important;
        }
        .atbd_content_upper {
          .atbd-list-body-top {
            padding-right: 40px;
          }
      
          .atbd-list-body-right {
            position: absolute;
            right: 17px;
            top: 20px;
          }
        }
    }
}

// Listing Cover Top Right
.atbd-list-cover-top-right {
    position: absolute;
    top: 20px;
    right: 20px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  
    .atbdp_add_to_fav_listings {
      .atbdp_mark_as_fav {
        display: flex !important;
      }
    }
  }