.atbd_content_active #directorist.atbd_wrapper {
    .atbd_give_review_area {
        .atbd_review_rating_area {
          display: inline-block;
          border: 1px solid $border-color;
          padding: 10px 20px;
          border-radius: 2px;
          margin-bottom: 20px;
    
          .atbd_review_current_rating {
            margin-bottom: 10px;
          }
    
          .atbd_review_current_rating .rating_label {
            margin-right: 13px;
          }
    
          .atbd_rating_label,
          .atbd_review_current_rating .atbd_rated_stars,
          .atbd_rating_stars {
            display: inline-block;
            vertical-align: middle;
          }
    
          .atbd_rating_label {
            margin-right: 10px;
          }
        }
    
        #atbd_up_preview {
          margin-bottom: 10px;
    
          .atbd_up_prev {
            width: 80px;
            height: 80px;
            position: relative;
            display: inline-block;
            border: 4px solid #dfe5ff;
            overflow: hidden;
    
            &+.atbd_up_prev {
              margin-left: 10px;
            }
    
            img {
              max-width: 100%;
            }
    
            .rmrf {
              position: absolute;
              top: 0;
              right: 0;
              background: #d4d3f3;
              color: $light;
              line-height: 15px;
              width: 15px;
              font-size: 11px;
              text-align: center;
              border-radius: 50%;
              cursor: pointer;
    
              &:hover {
                @extend .atbd_bg-danger;
              }
            }
          }
        }
    
        .form-group {
          textarea.form-control {
            padding-top: 10px !important;
          }
        }
    }
}

// Contact Listing Owner Form
.atbd_ads-form {
    .atbd_seach_fields_wrapper {
      border: 0 none !important;
    }
  }
  
  .contact-listing-owner-form {
    textarea {
      padding: 10px 15px !important;
      min-height: 120px;
    }
}

// Contact Owner
.contact_listing_owner {
    padding: 30px;
  
    textarea.form-control {
      padding-top: 10px !important;
    }
}
  
.hide-contant-owner {
    margin-top: 20px;
}